import Vue from 'vue'
import Vuex from 'vuex'
import router from "../router/index"
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: JSON.parse(localStorage.getItem("userInfo")) || "",
  },
  mutations: {
    login(state, res) {
      state.user = res
    }
  },
  actions: {
    login({ commit }, obj) {
      fetch(
        `http://adminv2.happymmall.com/manage/user/login.do?username=${obj.username}&password=${obj.password}`,
        {
          method: "POST",
        }
      )
        .then((res) => res.json())
        .then((res) => {
          if (res.status == 0) {
            localStorage.setItem("userInfo", JSON.stringify(res.data))
            commit("login", res.data)
            router.push({
              name: "Home"
            })
          }
        });
    }
  },
  modules: {
  }
})
