<template>
  <div class="header" v-if="boo">
    <div class="header_brand">
      <a href="javascript:">
        <b>HAPPY</b>
        MMALL
      </a>
    </div>
    <ul class="header_login">
      <li class="login">
        <a href="javascript:">
          <i class="fa fa-user" aria-hidden="true"></i>
          <span>欢迎，{{ $store.state.user.username }}</span>
          <ul class="out" @click="out">
            <li>
              <a href="javascript:">
                <i class="fa fa-sign-out" aria-hidden="true"></i>
                退出登录
              </a>
            </li>
          </ul>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      boo: true,
    };
  },
  methods: {
    out() {
      this.$router.push({
        name: "Login",
      });
      this.$store.commit("login", "");
      localStorage.clear("user");
    },
  },
  watch: {
    $route: {
      immediate: true,
      handler(val) {
        if (val.name == "Login") {
          this.boo = false;
        } else {
          this.boo = true;
        }
      },
    },
  },
};
</script>
<style scoped lang="scss">
.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  box-shadow: 0 3px 3px rgb(0 0 0 / 5%);
  z-index: 67;
  .header_brand {
    height: 60px;
    width: 260px;
    a {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100%;
      background-color: #32323a;
      color: #fff;
      font-size: 30px;
      font-weight: 700;
      padding: 15px;
      b {
        color: #2dafcb;
        font-weight: 700;
        line-height: 0.9;
      }
    }
  }
  .header_login {
    height: 60px;
    margin-bottom: 0 !important;
    padding: 0;
    .login {
      width: 170px;
      height: 61px;
      padding: 0 20px;
      box-sizing: border-box;
      position: relative;
      a {
        height: 100%;
        display: flex;
        align-items: center;
        color: #666;
        &:hover .out {
          height: 54px;
        }
        i {
          margin-right: 5px;
        }
        span {
          position: relative;
          margin-right: 10px;
          &::after {
            content: "";
            height: 0px;
            width: 0px;
            border: 5px solid;
            border-color: #666 transparent transparent transparent;
            position: absolute;
            top: 50%;
            right: -20px;
            transform: translateX(-50%);
          }
        }
        .out {
          position: absolute;
          top: 61px;
          right: 0;
          width: 230px;
          height: 0px;
          background-color: #fff;
          z-index: 33;
          border: 1px solid rgba(0, 0, 0, 0.15);
          border-bottom: none;
          box-shadow: 0 6px 12px rgb(0 0 0 / 18%);
          align-items: center;
          overflow: hidden;
          transition: height 0.1s;
          box-sizing: border-box;
          li {
            width: 100%;
            height: 100%;
            a {
              width: 100%;
              height: 100%;
              display: flex;
              align-items: center;
              color: #333;
              i {
                margin-right: 5px;
                margin-top: 3px;
              }
              &:hover {
                color: #225081;
              }
            }
          }
        }
      }
    }
  }
}
@media (max-width: 768px) {
  .header {
    display: inline-block;
    .header_brand {
      width: 100%;
      a {
        display: inline-block;
      }
    }
    .header_login {
      width: 100%;
      .login {
        a {
          &:hover .out{
            background-color: #fff;
          }
          .out {
            left: 0;
            background-color:#2b2e33;
          }
        }
      }
    }
  }
}
</style>
