<template>
  <div class="sidebar" v-if="boo">
    <ul class="ant_menu">
      <li class="ant_menu_submenu" v-for="item in sidebar" :key="item.id">
        <div class="ant_menu_submenu_title" @click="toggle(item.id)">
          <span>
            <i :class="item.icon" aria-hidden="true"></i>
            {{ item.title }}
          </span>
          <i
            class="fa fa-angle-down"
            :class="{ select: activeIndex === item.id }"
            aria-hidden="true"
          ></i>
        </div>
        <ul
          class="ant_menu_inline"
          :class="{
            select: activeIndex === item.id,
            background: activeBackground === item.name,
          }"
        >
          <router-link :to="{ name: item.name }" tag="li" class="ant_menu_item">
            <i class="fa fa-pie-chart" aria-hidden="true"></i>
            {{ item.con }}
          </router-link>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      boo: true,
      sidebar: [
        {
          id: 1,
          icon: "fa fa-bar-chart",
          title: "仪表盘",
          con: "商品",
          name: "Goods",
          boo: false,
        },
        {
          id: 2,
          icon: "fa fa-shopping-cart",
          title: "商品",
          con: "商品管理",
          name: "Product",
          boo: false,
        },
        {
          id: 3,
          icon: "fa fa-list",
          title: "品类",
          con: "品类管理",
          name: "Category",
          boo: false,
        },
        {
          id: 4,
          icon: "fa fa-check-square-o",
          title: "订单",
          con: "订单管理",
          name: "Order",
          boo: false,
        },
        {
          id: 5,
          icon: "fa fa-user-o",
          title: "用户",
          con: "用户管理",
          name: "User",
          boo: false,
        },
      ],
      activeIndex: 1,
      activeBackground: "",
    };
  },
  methods: {
    toggle(id) {
      this.activeIndex = id;
    },
  },
  watch: {
    $route: {
      immediate: true,
      deep: true,
      handler(val) {
        this.activeBackground = val.name;
        if (val.name == "Goods") {
          this.activeIndex = 1;
          document.title = "首页 - MMALL ADMIN";
          if (this.$store.state.user == "") {
            this.$router.push({
              name: "Login",
            });
          }
        } else if (val.name == "Product") {
          this.activeIndex = 2;
          document.title = "商品管理 - MMALL ADMIN";
        } else if (val.name == "Category") {
          this.activeIndex = 3;
          document.title = "品类管理 - MMALL ADMIN";
        } else if (val.name == "Order") {
          this.activeIndex = 4;
          document.title = "订单管理 - MMALL ADMIN";
        } else if (val.name == "User") {
          this.activeIndex = 5;
          document.title = "用户列表 - MMALL ADMIN";
        }
        if (val.name == "Login") {
          this.boo = false;
        } else {
          this.boo = true;
        }
      },
    },
  },
  mounted() {
    this.$forceUpdate();
  },
};
</script>

<style scoped lang="scss">
.sidebar {
  width: 260px;
  height: 100%;
  background-color: #2b2e33;
  z-index: 66;
  position: fixed;
  left: 0;
  top: 60px;
  .ant_menu {
    width: 100%;
    color: #fff;
    margin: 0 !important;
    padding: 0 !important;
    font-size: 15px;
    .ant_menu_submenu {
      width: 100%;
      cursor: pointer;
      .ant_menu_submenu_title {
        width: 100%;
        height: 55px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 15px;
        box-sizing: border-box;
        transition: opacity 0.1s;
        opacity: 0.8;
        &:hover {
          opacity: 1;
        }
        span {
          i {
            margin-right: 5px;
          }
        }
        i {
          &.select {
            transform: rotate(180deg);
          }
        }
      }
      .ant_menu_inline {
        width: 100%;
        height: 0px;
        overflow: hidden;
        margin: 0 !important;
        padding: 0 !important;
        background-color: #000;
        transition: height 0.1s;
        &.select {
          height: 55px;
        }
        &.background {
          background-color: #2dafcb;
          li {
            opacity: 1;
          }
          li:hover {
            opacity: 1;
          }
        }
        li {
          width: 100%;
          height: 100%;
          padding-left: 45px !important;
          box-sizing: border-box;
          display: flex;
          align-items: center;
          transition: opacity 0.1s;
          opacity: 0.8;
          &:hover {
            opacity: 1;
          }
          i {
            margin-right: 5px;
          }
        }
      }
    }
  }
}
@media (max-width: 768px) {
  .sidebar {
    position: static;
    height: auto;
    margin-top: 120px;
    width: 100%;
  }
}
</style>
